import { Box } from '@mui/material'
import React from 'react'
import { memberPageStyles } from './styles'
import { useSelector } from 'react-redux';
import { getAccountStatus, getAccountUserName } from "../../store/reducers/accountSlice";


const showUsers = () => {
  fetch('https://643e8178c72fda4a0bf87f06.mockapi.io/users')
  .then(response => response.json())
  .then((data => {console.log('USERS: ', data)}))
}

const addUser = () => {
  const user = {
    login: "Bill",
    password: "888",
    orders: ["book"]
    };

  fetch('https://643e8178c72fda4a0bf87f06.mockapi.io/users', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json; charset=utf-8'
    },
    body: JSON.stringify(user)
  })
  .then(response => response.json())
  .then((data => {console.log('NEW USER: ', data)}))
}

const deleteUser = () => {
  fetch('https://643e8178c72fda4a0bf87f06.mockapi.io/users/6', {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json'
    },
    body: null
  })
  .then(response => response.json())
  .then((data => {console.log('del USER: ', data)}))
}

export const MemberPage = () => {
  const hasAccess = useSelector(getAccountStatus)
  const currentUserName = useSelector(getAccountUserName)


  return (
<Box sx={memberPageStyles} >
  <div>MemberPage</div>
  <button onClick={()=>console.log('hasAccess: ', hasAccess, 'AccountUserName', currentUserName )} >show member</button>
  <button onClick={showUsers} >show users</button>
  <button onClick={addUser} >add user</button>
  <button onClick={deleteUser} >delete user</button>
</Box>


  )
}
