import { FC, useState, useEffect } from 'react';
import { Box } from '@mui/system';
import { Link } from 'react-router-dom';
import { BankName } from './BankName';
import { LogoIcon } from '../../icons/LogoIcon';

import { logoIconStyles, logoStyles } from './styles';
import { LogoName } from './LogoName';
import Icon from '../../Icon';
import { hasAccess } from '../../../utils/hasAccess';
import { useHeaderLogoData } from '../../../hooks/useHeaderLogoData';



type THeaderLogoProps = {
  headerTheme: string;
};

export const HeaderLogo: FC<THeaderLogoProps> = ({ headerTheme }) => {
  const [isBankNameShown, setIsBankNameShown] = useState(false);
  const [isAuth, setIsAuth] = useState(false);

  useEffect(()=>{
    console.log('effect! ', isAuth);
    setIsAuth(hasAccess()) 
  }, [isAuth])




  const toggleBankNameVisibility = () => setIsBankNameShown(!isBankNameShown);

  return (
    <Box>
      <Link style={{ textDecoration: 'none' }} to={isAuth ? '/member' : '/'}>
        <Box
          sx={logoStyles}
          onMouseEnter={toggleBankNameVisibility}
          onMouseLeave={toggleBankNameVisibility}>
          <Box sx={logoIconStyles}>
            <Icon name='BurgerIcon' headerTheme={headerTheme} />
          </Box>
          <LogoName isShown={isBankNameShown} headerTheme={headerTheme} />
        </Box>
      </Link>
    </Box>
  );
};
