import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import { Box } from "@mui/system";
import { Header } from "../../components/Header";
import { useSelector } from 'react-redux';
// import { Slider } from 'components/Slider';
// import { AppLinks } from 'components/AppLinks';
// import { LoginForm } from 'components/LoginForm';
// import { LinkButton } from 'components/LinkButton';
// import { BankInfoSection } from 'components/BankInfoSection';
import {
  mainPageStyles,
  mainPageContainerStyles,
  // loginBoxStyles,
  mainPageLayoutStyles,
  headerBoxStyles
} from "./styles";
import { Typography } from "@mui/material";

import {useAppDispatch} from '../../store'
import { getAccountStatus, logIn } from "../../store/reducers/accountSlice";
import { MainPageLayout } from "../MainPageLayout";






export const MainPage = () => {
  const navigate = useNavigate();
  const hasAccess = useSelector(getAccountStatus)
  const dispatch = useAppDispatch();

  useEffect(() => {hasAccess && navigate('/member');});

  return (
    <MainPageLayout>
<Box sx={mainPageLayoutStyles}>
      <button onClick={()=>console.log('hasAccess: ', hasAccess)} >show hasAccess</button>
     {/* <Box sx={headerBoxStyles}>
        <Header headerTheme="light1" />
      </Box> */}
      {/* <Slider /> */}
      <Box sx={mainPageContainerStyles}>
        <Box sx={mainPageStyles}>
          {!hasAccess && (
            <>
          <Typography> Зарегистрироваться </Typography>
          <button onClick={()=>{ dispatch(logIn('sasha')) } } >klick</button>
            </>

          )}
          {/* {!hasAccess && (
            <Box sx={loginBoxStyles}>
              <LoginForm />
              <LinkButton size='large' view='secondary' path='registrationPage'>
                Зарегистрироваться
              </LinkButton>
            </Box>
          )} */}
          {/* <AppLinks /> */}
        </Box>
      </Box>
      {/* <BankInfoSection /> */}
    </Box>
    </MainPageLayout>
    
    
  );
};
